import Alpine from 'alpinejs'
import persist from '@alpinejs/persist'
import Slider from './Slider.js'
import intersect from '@alpinejs/intersect';
import collapse from '@alpinejs/collapse';
import Swiper, { Pagination, Navigation, Autoplay } from 'swiper';
import 'swiper/css';
import 'swiper/css/pagination';
import anime from 'animejs/lib/anime.es.js';

Swiper.use([Pagination, Navigation, Autoplay]);

const body = document.body;
const select = e => document.querySelector(e);
const selectAll = e => document.querySelectorAll(e);

window.Alpine = Alpine

window.addEventListener('load', () => {
    //on build a box page so need to do some custom stuff
    if(document.body.classList.contains('awt-bab-open') && window.innerWidth > 767) {
        console.log('bab');
        const scrollableSection = document.querySelector('.bundle-scrollable-section');
        const sectionToHide = document.querySelector('.awtomatic-bab-options-container');

        if (scrollableSection.scrollTop > 100) {
            window.setTimeout(()=>{
                sectionToHide.classList.add('scroll-hide');
            },200);
        } else {
            sectionToHide.classList.remove('scroll-hide');
        }

        scrollableSection.addEventListener('scroll', () => {
            if(scrollableSection.scrollTop > 100) {
                window.setTimeout(() => {
                    if(!sectionToHide.classList.contains('scroll-hide')){
                        sectionToHide.classList.add('scroll-hide');
                    }
                }, 200);
            } else {
                window.setTimeout(() => {
                    sectionToHide.classList.remove('scroll-hide');
                }, 200);
            }
        });
    }
});

let ticker = document.querySelectorAll('.ticker');
ticker.forEach(function (el) {
    let tickerList = el.querySelector('.ticker--list');
    let tickerClone = tickerList.cloneNode(true);
    el.append(tickerClone);
    el.append(tickerClone.cloneNode(true));
    el.append(tickerClone.cloneNode(true));
    el.append(tickerClone.cloneNode(true));
    el.append(tickerClone.cloneNode(true));
    el.append(tickerClone.cloneNode(true));
    el.append(tickerClone.cloneNode(true));
    el.append(tickerClone.cloneNode(true));
    el.append(tickerClone.cloneNode(true));
});

Alpine.data('Slider', Slider)

Alpine.data('logoSlider', () => ({
    initSlider(element) {
        const swiper = new Swiper(element, {
            loop: true,
            autoplay: {
                delay: 2000,
            },
            navigation: {
                nextEl: ".slider-button-next",
                prevEl: ".slider-button-prev",
            },
            breakpoints: {
                // when window width is >= 480px
                200: {
                    slidesPerView: 2.5,
                    spaceBetween: 60,
                    centeredSlides: false,
                },
                // when window width is >= 640px
                640: {
                    slidesPerView: 8.5,
                    spaceBetween: 80,
                    centeredSlides: true,
                }
            }
        });
    }
}));

Alpine.data('includedSlider', () => ({
    initSlider(element) {
        const swiper = new Swiper(element, {
            loop: true,
            autoplay: true,
            slidesPerView: 4,
            spaceBetween: 30,
            loopAdditionalSlides: 5,
        });
    }
}));

let Sunrise = {
    updateQuantity(line, qty) {
        fetch('/cart/change.js', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ line: line, quantity: qty })
        }).then(response => response.json())
            .then(data => {
                console.log(data);
                // fire javascript event on window
                window.dispatchEvent(new Event('cart-updated'));
            })
            .catch((error) => {
                console.log('error time');
                console.error('Error:', error);
            });
    }
}

window.Anime = anime;
window.Sunrise = Sunrise;

Alpine.plugin(persist);
Alpine.plugin(intersect);
Alpine.plugin(collapse);

Alpine.start();
